import React from "react";
import "../assets/styles/Filmreview.scss";
import { Routes, Route } from "react-router-dom";
import FilmDetails from "./FilmDetails";
import reviews from "../data/reviews.json";

function FilmReview() {
  const handleRedirect = (path: string) => {
    window.location.href = path; // Redirect to the full URL
  };
  return (
    <div className="filmreview-container" id="filmreview">
      <h1>Film Review</h1>
      <div className="filmreview-grid">
        {reviews.map((review) => (
          <div
            key={review.id}
            className="filmreview"
            onClick={() => {
              handleRedirect(review.path);
            }}
          >
            <a
              // href="https://github.com/lechang-zhang/Film-BookReview/blob/main/Xiaowu.md"
              target="_blank"
              rel="noreferrer"
            >
              <img
                src={review.img}
                className="zoom"
                alt="thumbnail"
                width="100%"
              />
            </a>
          </div>
        ))}
      </div>
    </div>
  );
}

export default FilmReview;
