import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import "../assets/styles/Filmdetails.scss";
import reviews from "../data/reviews.json";
import ReactMarkdown from "react-markdown";

interface FilmDetailsProps {
  mode: string;
}

function FilmDetails({ mode }: FilmDetailsProps) {
  const { filmId } = useParams();
  const film = reviews.find((review) => review.id === filmId);
  const [content, setContent] = useState("");

  useEffect(() => {
    // Dynamically load the markdown content based on filmId
    import(`../contents/${filmId}.md`)
      .then((res) => fetch(res.default).then((r) => r.text()))
      .then((text) => setContent(text))
      .catch((err) => console.error(err));
  }, [filmId]);

  return (
    <div
      className={`film-details-container ${
        mode === "dark" ? "dark-mode" : "light-mode"
      }`}
    >
      <div className="film-details-content">
        {/* Top section with film image, title, author, and date */}
        <div className="film-header">
          <img className="film-image" src={film ? film.img : ""} alt="Xiaowu" />
          <div className="film-info">
            <h1>{film ? film.title?.replaceAll("-", " ") : ""}</h1>
            <p className="author-date">{`By ${film?.author} | ${film?.date}`}</p>
          </div>
        </div>

        {/* Review Content */}
        <div className="blog-content">
          <p>
            <ReactMarkdown>{content}</ReactMarkdown>
          </p>
        </div>
      </div>
    </div>
  );
}

export default FilmDetails;
