import React, { useState, useEffect } from "react";
import {
  Main,
  Timeline,
  Expertise,
  Project,
  FilmReview,
  FilmDetails,
  BookReview,
  Contact,
  Navigation,
  Footer,
} from "./components";
import {
  Routes,
  Route,
  useRoutes,
  useLocation,
  useMatch,
} from "react-router-dom";
import FadeIn from "./components/FadeIn";
import "./index.scss";

function App() {
  const [mode, setMode] = useState<string>("dark");

  const handleModeChange = () => {
    if (mode === "dark") {
      setMode("light");
    } else {
      setMode("dark");
    }
  };

  useEffect(() => {
    window.scrollTo({ top: 0, left: 0, behavior: "smooth" });
  }, []);

  const location = useLocation();
  // Use useMatch to check if the current route matches the film details route
  const isFilmDetailsPage = useMatch("/film-review/:filmId");

  return (
    <div
      className={`main-container ${
        mode === "dark" ? "dark-mode" : "light-mode"
      }`}
    >
      <Navigation
        parentToChild={{ mode }}
        modeChange={handleModeChange}
        isFilmDetails={isFilmDetailsPage}
      />
      <FadeIn transitionDuration={700}>
        <Routes>
          {/* Route for the main page */}
          <Route
            path="/"
            element={
              <>
                <Main />
                <FilmReview />
                <BookReview />
                <Project />
                <Timeline />
              </>
            }
          />

          {/* Dynamic route for individual film details */}
          <Route
            path="/film-review/:filmId"
            element={<FilmDetails mode={mode} />}
          />
        </Routes>
      </FadeIn>
      {/* <Footer /> */}
    </div>
  );
}

export default App;
